main.main {
    margin-top: 10rem;


    .btn {
        background-color: rgba(50, 50, 50, 1);
        cursor: pointer !important;
        color: white;
        font-size: 0.7em;
        height: 3.5rem;
        width: 13rem;
        margin-top: 1.3rem;
        transition: all 0.5s;

        &:hover {
            background-color: rgb(0, 0, 0);

        }
    }

    .container {
        margin-left: 13%;
    }

    #info-text {
        margin-top: 5rem;
        font-size: 1.2em;
        line-height: 1.6;

        p:first-of-type {
            font-weight: 700;
            position: relative;
        }

        p:first-of-type {
            font-weight: 700;
            position: relative;
            margin-bottom: 2rem;
        }

        p#his {
            margin-top: 5rem;
        }

        span.spanone {
            display: inline-block;
            border: 1px solid black;
            background-color: black;
            width: 5rem;
            height: 0.4rem;
            position: absolute;
        }

        span.spantwo {
            margin-top: 1rem;
            display: inline-block;
            border: 0.8px solid black;
            background-color: black;
            position: absolute;
            width: 5rem;
        }
    }

    div#img-con {

        width: 55vw;
        height: 76vh;
        background: url('../../Images/coffee.png');
        background-repeat: no-repeat;
        background-position: right;
        position: relative;
        background-attachment: fixed;
        // z-index: -1;

    }
}