.header {
    position: relative;
    color: white !important;
    height: 6vh;
    background-color: #fbfbfb;
    position: fixed;
    z-index: 100;
    top: 0;
    width: 100%;

    i {
        color: black;
        font-size: 1.2em;
        margin: 0 !important;
        padding: 0 !important;
        margin-top: 0.3rem !important;
        
       
    }

    a.nav-link {
        color: rgb(0, 0, 0) !important;
        margin-top: 1rem;
        padding-right: 4rem !important;
        font-size: 2em;
        font-weight: 700;
        margin-top: 0.2rem;
    }

    .navbar-toggler-icon {
        // background-color: rgb(114, 61, 18) !important;
        font-size: 1.5em;
    }

    .logo {
        position: absolute;
        top: -15px;
        left: 0;

        img {
            width: 8rem;
        }
    }

    div.dropdown-menu.show {
        width: 21rem;
        margin: 0 !important;
        padding: 0 !important;
        // text-align: center !important;
        z-index: 100;
        background-color: rgba(0, 0, 0, 0);
        border: none !important;
    }



}