 button.btn {
    background-color: #323232;
    cursor: pointer !important;
    color: white;
    font-size: 0.7em;
    height: 3.5rem;
    width: 12rem;
    margin-top: 1.3rem;
    transition: all 0.5s;
}

input.form-control {
    height: 4vh;
}

input[type="text"]
{
    font-size: 1em;
}

input[type="email"]
{
    font-size: 1em;
}

textarea.form-control {
    font-size: 1em;
}