#shopping-cart {
    max-width: 50%;
    margin: auto;
    font-size: 2em;
    margin-bottom: 10.5rem !important;


    h1 {
        font-size: 2.5em;
        margin-top: 10rem;
        margin-bottom: 4rem;
    }

    p {
        margin: 0 0 0 10px;

    }

    .cart-item {
        display: flex;
        flex-direction: row;
        align-items: center;

    }

    .cart-item img {
        max-height: 100px;
        max-width: 100px;
    }

    .alert {
        text-align: right;
        background: none;
        border: none;
        margin-top: 2rem;
        -webkit-box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.75);

    }

    .table {
        width: 50%;
        margin-left: auto;
    }

    button {
        background-color: #323232;
        cursor: pointer !important;
        color: white;
        font-size: 0.7em;
        height: 3.5rem;
        width: 12rem;
        margin-top: 1.3rem;
        transition: all 0.5s;
        border: none;
    }

    // span {
    //     position: absolute;
    //     top: 0.5%;
    //     right: 3%;
    //     z-index: 100;
    // }
}