.info-container {
    font-size: 2em;
    background: linear-gradient(rgba(175, 173, 173, 0.842), rgba(255, 255, 255, 0.541)),
        url('../../Images/coffeeBeans.jpg');
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    min-height: 50vh;

    .info-box {
        width: 100%;
        position: relative;
        top: 0;
        bottom: 0;
        line-height: 1.1;

        h2 {
            font-size: 1.3em;
            padding-top: 12rem;
            margin-top: 4rem;
            margin-bottom: 5rem;
            font-weight: 700;
            position: relative;
        }

        h2:after {
            display: block;
            position: absolute;
            left: 47.9%;
            bottom: -15%;
            width: 4vw;
            height: 2px;
            content: "";
            background: rgb(70, 70, 70);

        }

        .btn {
            background-color: rgba(50, 50, 50, 1);
            cursor: pointer !important;
            color: white;
            font-size: 0.7em;
            height: 3.5rem;
            width: 12rem;
            margin-top: 1.3rem;
            transition: all 0.5s;

            &:hover {
                background-color: rgb(0, 0, 0);

            }
        }

    }



    p.p1 {
        padding-left: 1rem;
    }

    p.p2 {
        padding-left: 1.5rem;
    }

    p.p3 {
        padding-right: 2rem;
    }

    p.p4 {
        padding-left: 5.7rem;
    }

    p.p5 {
        padding-right: 20rem;
    }

}

@media only screen and (min-device-width : 243px) and (max-device-width : 399px) {
    .info-box {
        width: 30rem !important;


        p {
            padding: 0 !important;
            font-size: 1.1em;
            margin-left: 6rem;
        }

        h2 {
            margin: 0rem !important;
            width: 36rem;
            font-size: 1.5em !important;
            padding-top: 1rem !important;
            padding-bottom: 1rem;
        }

        h2:after {
            display: none !important;
        }

        .btn {
            margin-left: 7rem;
        }

    }
}