.content-img {
    width: 100%;
    height: 85vh;
    background: linear-gradient(rgba(73, 73, 73, 0.342), rgba(87, 87, 87, 0.287)),
        url('../../Images/bg.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
    background-attachment: fixed;
    z-index: -1;

    .title {
        position: absolute;
        top: 40%;
        left: 27%;
        

        h1 {
            font-size: 7em;
            color: rgb(202, 202, 202);
        }

        p {
            font-size: 4em;
            font-style: italic;
            color: rgb(202, 202, 202);            
        }
    }
}

@media only screen and (min-device-width : 243px) and (max-device-width : 399px) {
    .title {
        left: 18% !important;

        h1 {
            font-size: 4em !important;
        }

    }


}